import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";

const bankOne = [
  {
    keyCode: 81,
    keyTrigger: "Q",
    id: "Chord-1",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3",
  },
  {
    keyCode: 87,
    keyTrigger: "W",
    id: "Chord-2",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3",
  },
  {
    keyCode: 69,
    keyTrigger: "E",
    id: "Chord-3",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3",
  },
  {
    keyCode: 65,
    keyTrigger: "A",
    id: "Shaker",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3",
  },
  {
    keyCode: 83,
    keyTrigger: "S",
    id: "Open-HH",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3",
  },
  {
    keyCode: 68,
    keyTrigger: "D",
    id: "Closed-HH",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3",
  },
  {
    keyCode: 90,
    keyTrigger: "Z",
    id: "Punchy-Kick",
    url: "https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3",
  },
  {
    keyCode: 88,
    keyTrigger: "X",
    id: "Side-Stick",
    url: "https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3",
  },
  {
    keyCode: 67,
    keyTrigger: "C",
    id: "Snare",
    url: "https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3",
  },
];

function App() {
  const [volume, setVolume] = useState(1);
  return (
    <div id="drum-machine">
      <div class="drumpad-container container-fluid">
        <div id="display"></div>
        {bankOne.map((audio) => {
          return (
            <Drumpad
              className="drum-pad"
              key={audio.id}
              audio={audio}
              volume={volume}
            />
          );
        })}
        <br />
        <p>Volume</p>
        <div class="slidecontainer">
          <input
            class="slider"
            type="range"
            step="0.01"
            onChange={(e) => setVolume(e.target.value)}
            value={volume}
            max="1"
            min="0"
          ></input>
        </div>
      </div>
    </div>
  );
}

function Drumpad({ audio, volume }) {
  useEffect(() => {
    document.addEventListener("keydown", handlerKeydown);
    return () => {
      document.removeEventListener("keydown", handlerKeydown);
    };
  });

  const handlerKeydown = (e) => {
    if (e.keyCode === audio.keyCode) {
      playAudio();
    }
  };

  const playAudio = () => {
    const audioElement = document.getElementById(audio.keyTrigger);
    audioElement.volume = volume;
    audioElement.play();
    document.getElementById("display").innerHTML = audio.id;
  };
  return (
    <div
      className="drum-pad btn btn-info p-3 m-2"
      id={audio.id}
      onClick={playAudio}
    >
      {audio.keyTrigger}
      <audio src={audio.url} className="clip" id={audio.keyTrigger}></audio>
    </div>
  );
}

ReactDOM.render(<App />, document.getElementById("root"));
